import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import ContractsList from "./contracts-list";

const Econtract = (props) => {
  const { match } = props;
  return (
    <Switch>
      <Redirect
        exact
        from={`${match.url}`}
        to={`${match.url}/contracts-list`}
      />
      <Route path={`${match.url}/contracts-list`} component={ContractsList} />
    </Switch>
  );
};

export default Econtract;
