import React, { lazy, Suspense } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import Loading from "components/shared-components/Loading";
import { APP_PREFIX_PATH } from "configs/AppConfig";
import ECommerce from "./ecommerce";
import AddProduct from "./ecommerce/add-product";
import Econtract from "./econtract";

export const AppViews = () => {
  return (
    <Suspense fallback={<Loading cover="content" />}>
      <Switch>
        <Route
          path={`${APP_PREFIX_PATH}/home`}
          component={lazy(() => import(`./home`))}
        />
        <Route path={`${APP_PREFIX_PATH}/ecommerce`} component={ECommerce} />
        <Route path={`${APP_PREFIX_PATH}/econtract`} component={Econtract} />
        <Route path={`${APP_PREFIX_PATH}/ecommerce`} component={AddProduct} />
        <Redirect from={`${APP_PREFIX_PATH}`} to={`${APP_PREFIX_PATH}/home`} />
      </Switch>
    </Suspense>
  );
};

export default React.memo(AppViews);
